body {
  color: #333;
  min-height: 100vh;
  background: linear-gradient(135deg, #f9f5f6 0%, #c890a7 50%, #a35c7a 100%);
  padding: 20px;
  font-family: Poppins, sans-serif;
  line-height: 1.6;
}

.project-details {
  max-width: 1200px;
  background: #ffffffe6;
  border-radius: 15px;
  margin: 0 auto;
  padding: 40px 20px;
  box-shadow: 0 8px 32px #0000001a;
}

.project-details h1 {
  text-align: center;
  color: #a35c7a;
  margin-bottom: 40px;
  font-size: 2.2rem;
  font-weight: 700;
}

.project-details section {
  background: #fffc;
  border-radius: 10px;
  margin-bottom: 40px;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

.project-details h2 {
  color: #a35c7a;
  margin-bottom: 20px;
  font-size: 2rem;
  font-weight: 600;
}

.project-details p {
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.project-details ul {
  padding-left: 40px;
  list-style-type: disc;
}

.project-details ul li {
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.image-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-bottom: 40px;
  display: grid;
}

.image-grid img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 4px 8px #0000001a;
}

.image-grid img:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px #0003;
}

.project-buttons {
  text-align: center;
  margin-top: 40px;
}

.cta-btn {
  color: #fff;
  background-color: #a35c7a;
  border-radius: 5px;
  margin: 10px;
  padding: 12px 24px;
  font-size: 1.2rem;
  font-weight: 600;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.cta-btn:hover {
  background-color: #c890a7;
}

.back-btn {
  color: #a35c7a;
  margin: 20px;
  font-size: 1.2rem;
  font-weight: 600;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.back-btn:hover {
  color: #c890a7;
}

.tech-logos {
  justify-content: center;
  gap: 30px;
  margin-bottom: 30px;
  display: flex;
}

.tech-item {
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

.tech-item i {
  font-size: 3rem;
}

.tech-item img {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

.tech-item span {
  color: #333;
  font-size: 1.1rem;
  font-weight: 600;
}

